@import url(https://fonts.googleapis.com/css?family=Oswald&display=swap);
*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: 'Oswald';
}
body{
  background-color: #010029;
}
.section-wrapper {
  width: 80%;
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
  margin: auto;
}

/*MEDIA QUERIES*/

@media (max-width: 420px){
  .section-wrapper {
    width: 100%;
    flex-direction: column;
  }
}
.intro-container {
    flex: 2 1;
    height: 9em;
}

.txt-container {
    flex: 1 1;
    height: 6em;
}

.intro-container span {
    color: #D5D5E0;
    font-size: 12vh;
}

.txt-container span {
    color: #D5D5E0;
    font-size: 16px;
}

.txt-container .typed-h1 span {
    font-size: 22px;
    font-weight: bolder;
}

@media (max-width: 420px){
    .intro-container, .txt-container {
        text-align: center;
        padding: 10px;
    }
  }


.bkgrndTxt {
    font-family: 'Courier New', Courier, monospace;
    opacity: 0.7;
    padding-left: 15px;
}

.bkgrndTxt:first-of-type {
    padding-top: 15px;
}

.background-wrapper {
    height: 100vh;
    background-color: #D5D5E0;
}

.aboutTxt-container {
    flex: 1 1;
    color: #010029;
}

.about-title {
    text-align: center;
    padding-top: 20px;
    color: #010029;
}

/*Flip Animation*/

.profileImg-container {
    flex: 1 1;
    display: flex;
    align-items: center;
    justify-content: center;
    overflow: hidden;
}

.front, .back {
    background-size: cover;
}

.c {
    position: absolute;
    width: 250px;
    height: 250px;
    cursor: pointer;
    border-radius: 12%;
    box-shadow: 0px 0px 15px 5px rgba(0, 0, 0, 0.35);
    will-change: transform, opacity;
}

.back {
    background-image: url(/my-website/static/media/back.d1c1f8d3.jpg);
}

.front {
    background-image: url(/my-website/static/media/profil.e7ec63de.jpg);
}

@media (max-width: 420px) {
    .bkgrndTxt {
        font-size: 12px;
    }
    .c {
        width: 150px;
        height: 150px;
    }
    .aboutTxt-container {
        font-size: 15px;
        text-align: center;
        padding: 10px;
    }
}
.card {
    color: #010029;
    background-color: #D5D5E0;
    width: 400px;
    height: 400px;
    display: flex;
    overflow: hidden;
    -webkit-transition: all .2s ease-in-out;
    transition: all .2s ease-in-out;
    box-shadow: 10px 10px 0px 0px rgba(137, 137, 169, 1);
}

.card:hover {
    -webkit-transform: scale(1);
    transform: scale(1);
}

.card-main {
    display: flex;
    flex-direction: column;
    justify-content: space-evenly;
    padding: 10px;
    flex: 2 1;
}

.cardText, .cardRole {
    font-size: 12px;
}

.card-side {
    padding: 10px;
    flex: 1 1;
    display: flex;
    flex-direction: column;
    justify-content: space-evenly;
    align-items: center;
    border-left-style: solid;
    border-left-color: #010029;
}

.card-side-logoContainer img {
    padding-top: 10px;
    padding-bottom: 10px;
    display: block;
    width: 35px;
}

@media (max-width: 420px) {
    .card {
        width: 250px;
        height: 250px;
    }
    .cardText, .cardRole {
        font-size: 9px;
    }
    .card h3 {
        font-size: 11px;
    }
    .card-side-logoContainer img {
        width: 25px;
    }
}
.btn {
    background-color: transparent;
    border: transparent;
    font-size: 20px;
    font-weight: bold;
    color: #D5D5E0;
    padding-right: 20px;
    padding-left: 20px;
    cursor: pointer;
}

/*Card Animation*/

.fade-enter {
    opacity: 0;
    z-index: 1;
}

.fade-enter.fade-enter-active {
    opacity: 1;
    -webkit-transition: opacity 300ms linear;
    transition: opacity 300ms linear;
}

.fade-exit {
    display: none;
}

.fade-exit.fade-exit-active {
    opacity: 0;
    -webkit-transition: opacity 300ms linear;
    transition: opacity 300ms linear;
}

.fade-exit-done {
    opacity: 0;
}

/*Footer*/

footer {
    display: flex;
    width: 100%;
    background-color: #D5D5E0;
}

footer img {
    width: 25px;
    -webkit-transition: all .2s ease-in-out;
    transition: all .2s ease-in-out;
}

footer img:hover {
    -webkit-transform: scale(1.1);
            transform: scale(1.1);
}

.footer-imgContainer {
    display: flex;
    align-items: center;
    margin-left: auto;
    margin-right: 20px;
}

.footer-imgContainer a {
    margin-left: 20px;
}

.footer-contactContainer {
    margin-left: 20px;
}

@media (max-width: 420px) {
    .project-wrapper .section-wrapper {
        flex-direction: row;
    }
}
